<!--
        添加房屋页面
-->
<template>
    <div>
        <!-- LOGO -->
        <el-row>
            <el-col :span="24">
                <img @click="$router.push({path: '/'})" src="/static/img/wehouseLogo.png" alt="Logo" style="width: 251px;">
            </el-col>
        </el-row>
        <br>
        <!-- 房源名称 -->
        <el-row>
            <el-col :span="12" :push="6">
            <el-input class="el-col-24" v-model="house.name">
                <template slot="prepend">房源名称</template>
            </el-input>
            </el-col>
        </el-row>
        <br>
        <!--  -->
        <el-row>
            <el-col :span="5" :push="6">
                <el-select id="rentalType" class="el-col-24" v-model="house.rentalType" placeholder="请选择出租方式">
                    <el-option style="text-align: center" label="出租方式: 整租" value="entireRent"></el-option>
                    <el-option style="text-align: center" label="出租方式: 合租" value="ShareRent"></el-option>
                </el-select>
            </el-col>

            <el-col :span="5" :push="8">
                <el-select class="el-col-24" v-model="house.orientation" placeholder="请选择房源朝向">
                    <el-option style="text-align: center" label="房源朝向: 东" value="east"></el-option>
                    <el-option style="text-align: center" label="房源朝向: 南" value="south"></el-option>
                    <el-option style="text-align: center" label="房源朝向: 西" value="west"></el-option>
                    <el-option style="text-align: center" label="房源朝向: 北" value="north"></el-option>
                </el-select>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col style="margin-top: 2vh;color: #8492a6" :span="2" :push="6">入住日期</el-col>
            <el-col :span="4" :push="6">
                <el-date-picker @change="changeDate" type="date" placeholder="选择日期" v-model="house.checkInTimeDay"  time-arrow-control style="width: 100%;"></el-date-picker>
            </el-col>
            <el-col :span="2" :push="6" style="margin-top: 2vh;color: #8492a6">入住时间</el-col>
            <el-col :span="4" :push="6">
                <el-time-picker @change="changeTime" placeholder="选择时间" arrow-control v-model="house.checkInTimeTime" style="width: 100%;"></el-time-picker>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="4" :push="6">
                <el-input class="el-col-24" v-model="house.area">
                    <template slot="prepend">面积</template>
                </el-input>
            </el-col>

            <el-col :span="4" :push="6">
                <el-input class="el-col-24" v-model="house.floor">
                    <template slot="prepend">楼层</template>
                </el-input>
            </el-col>

            <el-col :span="4" :push="6">
                <el-input class="el-col-24" v-model="house.price">
                    <template slot="prepend">价格</template>
                </el-input>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="3" :push="6">
                <el-input type="text" class="el-col-24" placeholder="广东省" v-model="house.provinceCnName" style="width: 100%;">
                    <template slot="prepend">省份</template>
                </el-input>
            </el-col>
            <el-col :span="3" :push="6">
                <el-input type="text" class="el-col-24" placeholder="广州市" v-model="house.cityCnName" style="width: 100%;">
                    <template slot="prepend">城市</template>
                </el-input>
            </el-col>
            <el-col :span="3" :push="6">
                <el-input type="text" class="el-col-24" placeholder="天河区" v-model="house.regionCnName" style="width: 100%;">
                    <template slot="prepend">区域</template>
                </el-input>
            </el-col>
            <el-col :span="3" :push="6">
                <el-input type="text" class="el-col-24" placeholder="龙洞街道" v-model="house.streetCnName" style="width: 100%;">
                    <template slot="prepend">街道</template>
                </el-input>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="4" :push="6">
                <el-input type="text" class="el-col-24" placeholder="" v-model="house.village" style="width: 100%;">
                    <template slot="prepend">小区</template>
                </el-input>
            </el-col>
            <el-col :span="4" :push="6">
                <el-input type="text" class="el-col-24" placeholder="n房m厅" v-model="house.houseType" style="width: 100%;">
                    <template slot="prepend">户型</template>
                </el-input>
            </el-col>
            <el-col :span="4" :push="6">
                <el-input type="text" class="el-col-24" placeholder="" v-model="house.ownerId" style="width: 100%;">
                    <template slot="prepend">房东id</template>
                </el-input>
            </el-col>
        </el-row>
        <br>
        <!-- 详细地址 -->
        <el-row>
            <el-col :span="12" :push="6">
                <el-input v-model="house.address">
                    <template slot="prepend">详细地址</template>
                </el-input>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="6" :push="6">
                <el-input class="el-col-24" v-model="house.subwayLineId">
                    <template slot="prepend">靠近的地铁线路id</template>
                </el-input>
            </el-col>

            <el-col :span="6" :push="6">
                <el-input class="el-col-12" v-model="house.subwayStationId">
                    <template slot="prepend">靠近的地铁站id</template>
                </el-input>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="6" :push="6">
                <el-input class="el-col-24" v-model="house.longitude">
                    <template slot="prepend">经度</template>
                </el-input>
            </el-col>
            <el-col :span="6" :push="6">
                <el-input class="el-col-24" v-model="house.latitude">
                    <template slot="prepend">纬度</template>
                </el-input>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.elevator"
                        active-text="有电梯"
                        inactive-text="无电梯"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>

            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.television"
                        active-text="有电视"
                        inactive-text="无电视"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>

            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.fridge"
                        active-text="有冰箱"
                        inactive-text="无冰箱"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.washingMachine"
                        active-text="有洗衣机"
                        inactive-text="无洗衣机"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>

            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.airConditioning"
                        active-text="有空调"
                        inactive-text="无空调"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>

            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.heater"
                        active-text="有暖气"
                        inactive-text="无暖气"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.bed"
                        active-text="有床"
                        inactive-text="无床"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>

            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.broadband"
                        active-text="有宽带"
                        inactive-text="无宽带"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>

            <el-col :span="4" :push="6">
                <el-switch
                        v-model="house.wardrobe"
                        active-text="有衣柜"
                        inactive-text="无衣柜"
                        active-color="#13ce66"
                        inactive-color="#ff4949"></el-switch>
            </el-col>
        </el-row>
        <br>
        <br>

        <el-button-group>
            <el-button @click="submitData" size="medium" type="primary" icon="el-icon-upload2">提交</el-button>
            <el-button @click="saveData" size="medium" type="primary" icon="el-icon-document-checked">保存</el-button>
        </el-button-group>
    </div>
</template>

<script>
export default {
  name: 'addHouses',
  data: function () {
    return {
      house: {
        //  房源名称
        name: '',
        // 出租方式
        rentalType: '',
        // 房源朝向
        orientation: '',
        // 面积
        area: '',
        // 楼层
        floor: '',
        // 价格
        price: '',
        // 入住时间
        checkInTimeDay: new Date(),
        checkInTimeTime: new Date(),
        checkInTime: '',
        // 是否有电梯
        elevator: false,
        // 户型：n房n厅
        houseType: '',
        // 省份中文名称
        provinceCnName: '',
        // 城市中文名称
        cityCnName: '',
        // 区域中文名称
        regionCnName: '',
        // 街道区域名称
        streetCnName: '',
        // 小区名称
        village: '',
        // 详细地址
        address: '',
        // 房东id
        ownerId: '',
        // 是否有电视
        television: false,
        //  是否有冰箱
        fridge: false,
        //  是否有洗衣机
        washingMachine: false,
        //  是否有空调
        airConditioning: false,
        //  是否有暖气
        heater: false,
        //  是否有床
        bed: false,
        //  是否有宽带
        broadband: false,
        //  是否有衣柜
        wardrobe: false,
        //  靠近的地铁线路id
        subwayLineId: '',
        //  靠近的地铁站id
        subwayStationId: '',
        //  房源详情
        description: '',
        //  经度
        longitude: '',
        //  维度
        latitude: ''
      },
      houseList: []

    }
  },
  computed: {
  },
  watch: {
    'house.checkInTime' () {
      console.log(this.house.checkInTime)
    }
  },
  created () {
  },

  methods: {
    changeDate (date) {
      this.house.checkInTime = (date.getFullYear() + '/' +
          '' + (date.getMonth() + 1).toString().padStart(2, '0') + '/' +
          '' + date.getDate().toString().padStart(2, '0') + ' ' +
          '' + this.house.checkInTime).trim()
    },
    changeTime (time) {
      this.house.checkInTime = (this.house.checkInTime + ' ' +
          '' + time.getHours().toString().padStart(2, '0') + ':' +
          '' + time.getMinutes().toString().padStart(2, '0') + ':' +
          '' + time.getSeconds().toString().padStart(2, '0')).trim()
    },
    submitData () {
      const that = this
      that.$http.post('/zufang/house', {
        name: that.house.name,
        rentalType: that.house.rentalType,
        orientation: that.house.orientation,
        area: that.house.area,
        floor: that.house.floor,
        price: that.house.price,
        checkInTime: that.house.checkInTime,
        elevator: that.house.elevator,
        houseType: that.house.houseType,
        provinceCnName: that.house.provinceCnName,
        cityCnName: that.house.cityCnName,
        regionCnName: that.house.regionCnName,
        streetCnName: that.house.streetCnName,
        village: that.house.village,
        address: that.house.address,
        ownerId: that.house.ownerId,
        television: that.house.television,
        fridge: that.house.fridge,
        washingMachine: that.house.washingMachine,
        airConditioning: that.house.airConditioning,
        heater: that.house.heater,
        bed: that.house.bed,
        broadband: that.house.broadband,
        wardrobe: that.house.wardrobe,
        subwayLineId: that.house.subwayLineId,
        subwayStationId: that.house.subwayStationId,
        description: that.house.description,
        longitude: that.house.longitude,
        latitude: that.house.latitude
      }, {headers: {'Content-Type': 'application/json'}})
        .then(function (response) {
          console.log(response)
          console.log(response.status)
          console.log(response.data)
          if (response.status === 200) {
            if (response.data.errorCode === 200) {
              console.log(response.data.data)
              alert('查看控制台信息')
            } else {
              alert(response.data.errorMsg)
            }
          }
        })
        .catch(function (error) {
          console.log(error)
          // alert(error)
        })
    },
    saveData () {
      alert('开发中...')
    }
  },
  filters: {
    // 格式化时间
    timeFormatter (e) {
      const year = e.getFullYear()
      const month = (e.getMonth() + 1).toString().padStart(2, '0')
      const day = e.getDate().toString().padStart(2, '0')
      return `${year}-${month}-${day}`
    }
  }

}
</script>

<style scoped>
html {
    scroll-behavior: smooth;
    overflow: scroll;
}

</style>



